import React, { Component, createElement } from 'react'
import List from './list'
import Input from './input'

export default class TodoList extends Component {
    constructor() {
        super();
        this.state = {
            inpValue: '',
            todos: [{ title: '1', done: false },
            { title: '2', done: true },
            { title: '3', done: true },
            { title: '4', done: false }]
        }
    }

    handleChange = (e) => {//数据可以输入到界面
        this.setState({ inpValue: e.target.value })

    }
    componentDidMount() {//在组件挂载阶段执行在render之后,只执行一次
        let todos = localStorage.getItem('todos');
        if (todos) {
            this.setState({
                todos: JSON.parse(todos)
            })
        }
    }
    componentDidUpdate(preProps, preState) {//是否要更新页面，在更新的时候保留上次做的操作,在inpvalue发生变化时才存储，输入时不存储
        if (preState.todos !== this.state.todos) {
            localStorage.setItem(
                'todos',
                JSON.stringify(this.state.todos)

            )
        }


    }
    addTodo = () => {//添加数据到界面

        if (this.state.inpValue == '') {//当输入是空值的时候不添加
            return;
        }
        else {
            this.setState({
                todos: [
                    {
                        title: this.state.inpValue,
                        done: false
                    },
                    ...this.state.todos//...展开运算符，扩展运算符
                ]
            })
            this.setState({ inpValue: '' });//异步执行，不一定谁先执行
        }
    }
    removeTodos = (idx) => {//删除数组中的数据行

        this.setState({//新的数组在比较的时候好比较，和原来不同
            todos: this.state.todos.filter((item, index) => index != idx)//只要index不等于idx的就返回过来
        }
        )//setState是异步执行的，这样子就可以解决同步存储之间的关系

    }
    toggle = (index) => {
        let newTodos = JSON.parse(JSON.stringify(this.state.todos));//深拷贝出来一份,地址会变成新的地址（变成一个字符串在变成一个对象）
        newTodos[index].done = !newTodos[index].done;
        this.setState({
            todos: newTodos

        })
    }


    render() {
        const { todos, inpValue } = this.state;
        let arr1 = todos.filter((item, index) => item.done);
        let arr2 = todos.filter((item, index) => !item.done);


        return (

            <div style={{ backgroundColor: 'MediumOrchid' }}>
                <Input inpValue={inpValue} handleChange={this.handleChange} addTodo={this.addTodo} />
                <h2 style={{ color: 'grey' }}>正在进行        {arr2.length}   </h2>
                <List done='false' data={todos} toggle={this.toggle} removeTodos={this.removeTodos} />
                <h2 style={{ color: 'grey' }}>已经完成        {arr1.length}   </h2>
                <List done='true' data={todos} toggle={this.toggle} removeTodos={this.removeTodos} />
            </div>
        )
    }
}

