import React from 'react'
import { connect } from 'react-redux' //函数，哪一个想要引入那个store引入即可

// connect
const Todos = (props) => {
    console.log(props);
    return (
        <div>
           {
                 props.todos.map((item) => <li>{item}</li>)
           }
           <button onClick={() => {
                props.dispatch(
                    {
                        type: 'add_todo',
                        value: 'hanyumeng'
                    }
                )
            }}>添加</button>
        </div>
    )
}

export default connect((state)=>{
    return {
        todos:state//就是状态容器中的参数
    }
})(Todos)//高阶函数 ，connect的返回值是一个组件，再将后面的值传给这个函数
// 将state的值注入到props中
