{/* 商品详情页 */ }
const Child = (props) => {
    console.log('../taobao' + listArr[props.location.index].src.slice(1));
    //获取图片下标
    const btn = ['店铺', '客服', '收藏', '加入购物车', '立即购买']
    return <div>
        {/* <NavBar
            style={{ backgroundColor: 'red', opacity: '0.3' }}
            mode="light"
            icon={<Icon type="left" />}
            // 返回上一级页面
            onLeftClick={() => props.history.go(-1)}
        ></NavBar> */}


        <li
            style={{
                listStyle: 'none',
                display: 'inline-block',
                width: '100%',
                height: '300px',

            }}
        >
            <div style={{ position: 'absolute' }}><button
                style={{ backgroundColor: 'grey', opacity: '1', width: '40px', height: '40px', borderRadius: '50%', opacity: '0.5', marginLeft: '20px', marginTop: '20px' }}
                mode="light"
                // icon={<Icon type="left" />}
                // 返回上一级页面
                onClick={() => props.history.go(-1)}
            >＜</button></div>
            <div style={{ position: 'relative', zIndex: '-1' }}><img style={{ width: '100%', height: '300px', }} src={listArr[props.location.index].src} /></div>
            {/* 需要修改路径                                                                src={require('../taobao'+listArr[props.location.index].src.slice(1))}   */}
            {/* <div  style={{fontSize:'1.1rem'}}> <div><span style={{color:'red',fontSize:'1.5rem'}}>{props.location.price}</span><br/>{props.location.show }</div></div> */}
        </li>
        <div style={{
            position: 'fixed',
            bottom: 0,
            width: '100%',
            height: '30px',
            backgroundColor: 'rgb(230,230,250)'
        }}>
            {btn.map((item, idx) => <button key={idx} style={{ marginLeft: '20px', marginTop: '5px', backgroundColor: 'rgb(230,230,250)' }}>{`${item}`}</button>)}
        </div>

    </div>
}

export default withRouter(Child)
