import { SearchBar, WingBlank } from 'antd-mobile'
import React from 'react'

//商品搜索页面

const Details = (props) => {
    return (
        <div>
            {/* <WingBlank><div className="sub-title">Normal</div></WingBlank> */}
            <SearchBar
                placeholder="宝贝"//默认搜索框的文字
                maxLength={8}//最大输入长度
                // 取消事件触发函数
                showCancelButton//显示取消按钮
                onCancel={() => {
                    props.history.go(-1);//返回上一级
                }} />


        </div>
    )
}

export default Details
