import React, { Component } from 'react'

export default class List extends Component {

  renderTodos = (done) => {
    const todos = this.props.data;
    return todos.map((todo, idx) => {
      if (done == String(todo.done)) {
        return <li key={todo.title}>
          <input
            onClick={() => this.props.toggle(idx)}
            checked={todo.done}
            type="checkbox"
          />
          <span style={{ color: 'grey' }} dangerouslySetInnerHTML={{ __html: todo.title }}></span>
          {/* 防止危险的输入 */}
          <button style={{ color: 'brown' }} onClick={() => this.props.removeTodos(idx)}>删除</button>
          {/* 箭头函数才能实现触发事件的时候传递参数 */}
        </li>
      }
      else {

      }
    })
  }


  render() {
    return (
      <ul>
        {this.renderTodos(this.props.done)}
      </ul>
    )
  }
}
